const ElementMoveable: React.FC<{
  wrapper: HTMLDivElement | null,
  containerRef: React.MutableRefObject<HTMLElement>,
  selectedElementType: string | null,
  selectedTargets: Array<HTMLElement | SVGElement>,
  elementResizableType: string,
  setElementResizableType: (type: string) => void,
  currentBox: HTMLElement | null,
}> = ({
  wrapper,
  containerRef,
  selectedElementType,
  selectedTargets,
  elementResizableType,
  setElementResizableType,
  currentBox,
 }) => {
  const bounds = useMemo(() => {
    const containerRect = containerRef.current.getBoundingClientRect()

    let parent = null
    parent = currentBox ? currentBox : wrapper

    if (parent) {
      const parentRect = parent.getBoundingClientRect()
      const parentTop = parentRect.top + window.scrollY
      const parentRight = parentRect.right
      const parentLeft = parentRect.left - containerRect.left
      const parentBottom = parentRect.bottom + window.scrollY - (containerRect.top + window.scrollY)
      return { left: parentLeft, right: parentRight, top: parentTop, bottom: parentBottom }
    } else {
      return {}
    }
  }, [selectedTargets, currentBox, wrapper])

  const elementGuidelines = useMemo(() => {
    if (currentBox) {
      return [currentBox, ...(currentBox ? Array.from(currentBox.children) : [])].filter(el => {
          return selectedTargets.indexOf(el as any) === -1;
      });
    }
    return [wrapper, ...(wrapper ? Array.from(wrapper.children) : [])].filter(el => {
        return selectedTargets.indexOf(el as any) === -1;
    });
  }, [selectedTargets])


  return (
    <>
      <Moveable<DimensionViewableProps&ResizableTypeViewableProps>
        ables={[DimensionViewable, ResizableTypeViewable]}
        dimensionViewable={true}
        resizableTypeViewable={selectedElementType === "rect"}
        elementResizableType={elementResizableType}
        setElementResizableType={setElementResizableType}
        targets={selectedTargets}
        container={containerRef.current}
        origin={false}
        edge={false}
        draggable={true}
        rotatable={true}
        resizable={elementResizableType === 'resizable'}
        clippable={elementResizableType === 'warpable'}
        clipArea={true}
        clipVerticalGuidelines={[0, "50%", "100%"]}
        clipHorizontalGuidelines={[0, "50%", "100%"]}
        clipTargetBounds={true}
        defaultClipPath={"polygon"}
        onClip={(e) => {
          if (e.clipType === "rect") {
            e.target.style.clip = e.clipStyle;
          } else {
            e.target.style.clipPath = e.clipStyle;
          }
        }}
        roundable={true}
        snappable={true}
        bounds={bounds}
        throttleDrag={0}
        edgeDraggable={true}
        snapDirections={{ top: true, left: true, right: true, center: true, middle: true }}
        elementSnapDirections={{ top: true, left: true, right: true, center: true, middle: true }}
        snapGap={false}
        isDisplayInnerSnapDigit={true}
        elementGuidelines={elementGuidelines as any}
        onDragStart={({ target, clientX, clientY }) => {}}
        onDrag={({
            target,
            beforeDelta, beforeDist,
            left, top,
            right, bottom,
            delta, dist,
            transform,
            clientX, clientY,
        }: OnDrag) => {
            target!.style.left = `${left}px`;
            target!.style.top = `${top}px`;
        }}
        onDragEnd={({ target, isDrag, clientX, clientY }) => {
          console.log(target)
        }}
        onDragGroup={({ targets, events }) => {
          events.forEach(({ target, beforeTranslate, left, top }, i) => {
            target!.style.left = `${left}px`;
            target!.style.top = `${top}px`;
          });
        }}
        keepRatio={false}
        throttleResize={0}
        onResize={({
            target, width, height,
            dist, delta, direction,
            clientX, clientY
        }: OnResize) => {
            if (!wrapper) return
            const rectTarget = currentBox ? currentBox : wrapper
            const wrapperRect = rectTarget.getBoundingClientRect()
            if (direction[0] === -1 && direction[1] === -1) {
              delta[0] && (target!.style.width = `${width}px`) && (target!.style.left = `${clientX - wrapperRect.left}px`)
              delta[1] && (target!.style.height = `${height}px`) && (target!.style.top = `${clientY + window.scrollY - (window.scrollY + wrapperRect.top)}px`)
            } else {
              if (direction[0] === -1 && (direction[1] === 0 || direction[1] === 1) ) {
                delta[0] && (target!.style.width = `${width}px`) && (target!.style.left = `${clientX - wrapperRect.left}px`)
              } else {
                delta[0] && (target!.style.width = `${width}px`)
              }
              if (direction[1] === - 1 && (direction[0] === 0 || direction[0] === 1)) {
                delta[1] && (target!.style.height = `${height}px`) && (target!.style.top = `${clientY + window.scrollY - (window.scrollY + wrapperRect.top)}px`)
              } else {
                delta[1] && (target!.style.height = `${height}px`)
              }
            }
        }}
        onResizeGroup={({ events, direction }) => {
          if (!wrapper) return
          const rectTarget = currentBox ? currentBox : wrapper
          const wrapperRect = rectTarget.getBoundingClientRect()
          events.forEach((ev, i) => {
            if (direction[0] === -1 && direction[1] === -1) {
              ev.delta[0] && (ev.target.style.width = `${ev.width}px`) && (ev.target.style.left = `${ev.clientX - wrapperRect.left}px`)
              ev.delta[1] && (ev.target.style.height = `${ev.height}px`) && (ev.target.style.top = `${ev.clientY + window.scrollY - (window.scrollY + wrapperRect.top)}px`)
            } else {
              if (direction[0] === -1 && (direction[1] === 0 || direction[1] === 1) ) {
                ev.delta[0] && (ev.target.style.width = `${ev.width}px`) && (ev.target.style.left = `${ev.clientX - wrapperRect.left}px`)
              } else {
                ev.delta[0] && (ev.target.style.width = `${ev.width}px`)
              }
              if (direction[1] === - 1 && (direction[0] === 0 || direction[0] === 1)) {
                ev.delta[1] && (ev.target.style.height = `${ev.height}px`) && (ev.target.style.top = `${ev.clientY + window.scrollY - (window.scrollY + wrapperRect.top)}px`)
              } else {
                ev.delta[1] && (ev.target.style.height = `${ev.height}px`)
              }
            }
          });
        }}
        throttleRotate={0}
        onRotate={({
            target,
            delta, dist,
            transform,
            clientX, clientY,
        }: OnRotate) => {
            target!.style.transform = transform;
        }}
        onWarp={({
            target,
            clientX,
            clientY,
            delta,
            dist,
            multiply,
            transform,
        }) => {
            target.style.transform = transform;
        }}
        onRound={e => {
          e.target.style.borderRadius = e.borderRadius;
        }}
        onClick={e => {
            const target = e.inputTarget as any;
            if (e.isDouble && target.hasAttribute('contenteditable')) {
              target.contentEditable = "true"
              target.focus();
            }
        }}
        />
    </>
  )
}

