const MoveableSelect: React.FC<{
  setSelectedElementType: (type: string | null) => void,
  setSelectedTargets: (targets: Array<HTMLElement | SVGElement>) => void,
  setElementResizableType: (type: string) => void,
  currentBox: HTMLElement | null,
  setCurrentBox: (box: HTMLElement | null) => void,
  selectedTargets: Array<HTMLElement | SVGElement>,
  selecToRef: React.RefObject<Selecto>
}> = ({
  setSelectedElementType,
  setSelectedTargets,
  setElementResizableType,
  currentBox,
  setCurrentBox,
  selectedTargets,
  selecToRef
 }) => {
  const selectableTargetOptions = useMemo(() => {
    if (currentBox) {
      return { selector: [`.box${`[data-sb-element=${currentBox.getAttribute('data-sb-element')}]`} > [data-sb-element-type]`], selectFromInside: true }
    } else {
      return { selector: ['.box', '[data-sb-element-type]'], selectFromInside: false }
    }
  }, [currentBox])
  return (
    <Selecto
      ref={selecToRef}
      getElementRect={getElementInfo}
      dragContainer={'.wrapper'}
      hitRate={0}
      selectableTargets={selectableTargetOptions.selector}
      selectByClick={true}
      selectFromInside={selectableTargetOptions.selectFromInside}
      toggleContinueSelect={["shift"]}
      preventDefault={true}
      onDragStart={e => {
        if (!selectableTargetOptions.selectFromInside || selectedTargets.length > 0) e.preventDrag()
      }}
      onSelect={e => {
        setSelectedTargets([...e.selected])
        setElementResizableType('resizable')
        if (e.removed.length > 0) {
          e.removed.forEach(ele => {
            if (ele.hasAttribute('contenteditable')) {
              const hasContentEditableNode = ele as HTMLElement
              hasContentEditableNode.contentEditable = "false"
            }
          })
        }
        if (e.selected.length === 0) {
          setSelectedElementType("")
          setCurrentBox(null)
          return
        }
        if (e.selected.length === 1) {
          const parent = e.selected[0].parentNode as HTMLElement
          if (parent && parent.getAttribute('data-sb-element-type') === 'box') {
            setCurrentBox(parent)
          } else {
            setCurrentBox(null)
          }
          const type = e.selected[0].getAttribute('data-sb-element-type')
          if (e.selected[0].classList.contains('box')) {
            const target = e.selected[0] as HTMLElement
            setCurrentBox(target)
            setSelectedTargets([])
          }
          setSelectedElementType(type)
        }
        if (e.selected.length > 1) {
          setSelectedElementType('multiple')
        }
      }}
      onSelectEnd={({ isDragStart, selected, inputEvent, rect }) => {
      }}
    >
    </Selecto>
  )
}

const ElementPanel: React.FC<{
  wrapper: HTMLDivElement | null,
  currentBox: HTMLElement | null,
  selectedElementType: string | null,
  setHtml: (value: string) => void,
  domTree: Array<{ parent: Element, children: Array<Element> | null }>,
  setCurrentBox: (box: HTMLElement | null) => void,
  setSelectedTargets: (targets: Array<HTMLElement | SVGElement>) => void,
}> = ({
  wrapper,
  currentBox,
  selectedElementType,
  setHtml,
  domTree,
  setCurrentBox,
  setSelectedTargets
}) => {
  const inputRef = useRef<HTMLInputElement | null>(null)
  const inputBackgroundImageRef = useRef<HTMLInputElement | null>(null)

  const [anchorEl, setAnchorEl] = React.useState<HTMLButtonElement | null>(null);
  const [tableIndex, setTableIndex] = useState<{ vertical: number, horizontal: number }>({ vertical: 1, horizontal: 1 })

  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const id = open ? 'table-popover' : undefined;

  const appendElement = (element: HTMLElement | SVGElement) => {
    if (!element.classList.contains('box')) {
      element.style.left = `${window.innerWidth / 2}px`
      element.style.top = `${window.scrollY}px`
    }
    if (!element.classList.contains('box') && selectedElementType === "box" && currentBox) {
      const boxRect = currentBox.getBoundingClientRect()
      element.style.top = `${-(boxRect.top) < 0 ? 0 : -(boxRect.top)}px`
      currentBox.appendChild(element)
      return
    }
    if (wrapper) wrapper.appendChild(element)
  }

  const addBox = () => {
    const box = document.createElement("div")
    box.classList.add('box')
    box.setAttribute('style', 'position: relative; max-width: 100%; max-height: 100%; width: 500px; height: 500px; margin: 0 auto;')
    box.setAttribute('data-sb-element', createUniqueString())
    box.setAttribute('data-sb-element-type', 'box')
    appendElement(box)
  }

  const add = () => {
    const paragragh = document.createElement("p")
    paragragh.setAttribute('data-sb-element', createUniqueString())
    paragragh.setAttribute('data-sb-element-type', 'text')
    paragragh.setAttribute('style', 'position: absolute; width: 100px; height: 50px; display: flex; justify-content: center; align-items: center; flex-direction: column; text-align: center; font-weight: 400; text-decoration: none; margin: 0; white-space: pre-wrap; word-break: break-all;')
    paragragh.innerText = "テキスト"
    paragragh.contentEditable = "false"
    // const paragragh = document.createElement("p")
    // paragragh.innerText = "テキスト"
    // paragragh.contentEditable = "true"
    // paragragh.setAttribute('style', 'text-align: center; font-weight: 100; text-decoration: none; margin: 0;')
    // wrapper.appendChild(paragragh)
    appendElement(paragragh)
  }

  const addTitle = (num: number) => {
    const title = document.createElement(`h${num}`)
    title.setAttribute('data-sb-element', createUniqueString())
    title.setAttribute('data-sb-element-type', 'text')
    title.setAttribute('style', "position: absolute; max-width: 100%; max-height: 100%; width: 300px; height: 100px; display: flex; justify-content: center; flex-direction: column;  align-items: center; text-align: center; text-decoration: none; margin: 0; white-space: pre-wrap; word-break: break-all;")
    title.innerText = "タイトル"
    title.contentEditable = "true"
    appendElement(title)
  }

  const clickFileFormImage = () => {
    if (inputRef.current) inputRef.current.click()
  }
  const clickBackgroundImageFileForm = () => {
    if (inputBackgroundImageRef.current) inputBackgroundImageRef.current.click()
  }

  const addImage = (e: React.ChangeEvent<HTMLInputElement>) => {
    e.persist()
    const files = e.target.files
    const createObjectURL = (window.URL || window.webkitURL).createObjectURL;
    if (files && files[0]) {
      const imageUrl = createObjectURL(files[0]);
      const imgWrapper = document.createElement("div")
      imgWrapper.setAttribute('data-sb-element', createUniqueString())
      imgWrapper.setAttribute('data-sb-element-type', 'image')
      imgWrapper.setAttribute('style', "position: absolute; max-width: 100%; max-height: 100%; width: 100px; height: 100px;")
      const img = document.createElement('img')
      img.setAttribute('src', imageUrl)
      img.setAttribute('style', "width: 100%; height: 100%;")
      imgWrapper.appendChild(img)
      appendElement(imgWrapper)
    }

  }

  const addBackgroundImage = (e: React.ChangeEvent<HTMLInputElement>) => {
    e.persist()
    const files = e.target.files
    const createObjectURL = (window.URL || window.webkitURL).createObjectURL;
    if (files && files[0]) {
      const imageUrl = createObjectURL(files[0]);
      const div = document.createElement("div")
      div.setAttribute('data-sb-element', createUniqueString())
      div.setAttribute('data-sb-element-type', 'image')
      div.setAttribute('style', `position: absolute; max-width: 100%; max-height: 100%; width: 500px; height: 100px; background: url(${imageUrl}) center top repeat-x;`)
      appendElement(div)
    }
  }

  const addVideo = () => {
    const div = document.createElement("div")
    div.setAttribute('data-sb-element', createUniqueString())
    div.setAttribute('data-sb-element-type', 'youtube')
    div.setAttribute('style', `position: absolute; max-width: 100%; max-height: 100%; width: 500px; height: 315px;`)
    div.innerHTML = '<iframe style="width: 100%; height: 100%;" src="https://www.youtube.com/embed/Ly8D0zAQTyo" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>'
    appendElement(div)
  }

  const addButton = () => {
    const button = document.createElement("div")
    button.setAttribute('data-sb-element', createUniqueString())
    button.setAttribute('data-sb-element-type', 'button')
    button.setAttribute('style', "display: flex; justify-content: center; align-items: center; text-align: center; position: absolute; max-width: 100%; max-height: 100%; width: 200px; height:50px; border-radius: 20px; background-color: #1976d2; color: #fff; padding: 0; border: none;")
    const span = document.createElement("span")
    span.innerText = "ボタン"
    button.appendChild(span)
    appendElement(button)
  }

  const addRect = () => {
    const rectWrapper = document.createElement("div")
    rectWrapper.setAttribute('data-sb-element', createUniqueString())
    rectWrapper.setAttribute('data-sb-element-type', 'rect')
    rectWrapper.setAttribute('style', "position: absolute; max-width: 100%; max-height: 100%; width: 200px; height: 100px; background-color: #1976d2; box-sizing: border-box;")
    appendElement(rectWrapper)
  }

  const addRoundRect = () => {
    const roundRectWrapper = document.createElement("div")
    roundRectWrapper.setAttribute('data-sb-element', createUniqueString())
    roundRectWrapper.setAttribute('data-sb-element-type', 'roundRect')
    roundRectWrapper.setAttribute('style', "position: absolute; max-width: 100%; max-height: 100%; width: 100px; height: 100px; background-color: #1976d2; border-radius: 50%; box-sizing: border-box;")
    appendElement(roundRectWrapper)
  }

  const addTable = () => {
    const table = document.createElement("table");
    table.setAttribute('data-sb-element', createUniqueString())
    table.setAttribute('data-sb-element-type', 'table')
    table.setAttribute('style', "position: absolute; max-width: 100%; max-height: 100%; width: 200px; height: 100px; background-color: #fff;")
    const tbody = document.createElement("tbody");
    ([...Array(tableIndex.horizontal)] as Array<undefined>).forEach(() => {
      const tr = document.createElement('tr');
      ([...Array(tableIndex.vertical)] as Array<undefined>).forEach(() => {
        const td = document.createElement('td');
        tr.appendChild(td)
      })
      tbody.appendChild(tr)
    })
    table.appendChild(tbody)
    appendElement(table)
  }

  const save = () => {
    if (wrapper) {
      setHtml(wrapper.outerHTML)
    }
  }
  return (
    <>
      <Button fullWidth onClick={() => addTitle(1)}>
        タイトル
      </Button>
      <Button fullWidth onClick={addBox}>
        Box
      </Button>
      <Button fullWidth onClick={add}>
        テキスト
      </Button>
      <Button fullWidth onClick={clickFileFormImage}>
        画像
        <Box ref={inputRef} component="input" type="file" sx={{ display: "none" }} onChange={addImage}>
        </Box>
      </Button>
      <Button fullWidth onClick={clickBackgroundImageFileForm}>
        背景画像
        <Box ref={inputBackgroundImageRef} component="input" type="file" sx={{ display: "none" }} onChange={addBackgroundImage}>
        </Box>
      </Button>
      <Button fullWidth onClick={addVideo}>
        動画
      </Button>
      <Button fullWidth onClick={addButton}>
        ボタン
      </Button>
      <Button fullWidth onClick={addRect}>
        長方形
      </Button>
      <Button fullWidth onClick={addRoundRect}>
        円形
      </Button>
      <Button fullWidth onClick={handleClick}>
        表
      </Button>
      <DndProvider backend={HTML5Backend}>
        <DomTreePopover
          wrapper={wrapper}
          domTree={domTree}
          setCurrentBox={setCurrentBox}
          setSelectedTargets={setSelectedTargets}
        />
      </DndProvider>
      <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
         transformOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
        PaperProps={{
          sx: { p: 2 }
        }}
      >
        <Box
          sx={{
            display: "grid",
          }}
        >
          {
            [...Array(tableIndex.vertical + 1)].map((_, verticalIndex) => {
              return (
                [...Array(tableIndex.horizontal + 1)].map((_, i) => {
                  return (
                    <Box
                      data-vertical-index={verticalIndex + 1}
                      data-horizontal-index={i + 1}
                      sx={{
                        gridRow: verticalIndex + 1,
                        gridColumn: i + 1,
                        mx: 0.3,
                        mb: 0.3,
                        width: "20px",
                        height: "20px",
                        border: "1px solid #000",
                        backgroundColor: `${tableIndex.vertical >= verticalIndex + 1 && tableIndex.horizontal >= i + 1 ? "red" : "" }`
                       }}
                       onMouseOver={() => setTableIndex({ vertical: verticalIndex + 1, horizontal: i + 1 })}
                       onClick={addTable}
                    >
                    </Box>
                  )
                })
              )
            })
          }
        </Box>
        <Box sx={{ textAlign: "center" }}>
          {tableIndex.vertical} × {tableIndex.horizontal}
        </Box>
      </Popover>
      <Button fullWidth onClick={save}>
        保存
      </Button>
    </>
  )
}

type DimensionViewableProps =  {
  dimensionViewable?: boolean;
}
const DimensionViewable = {
    name: "dimensionViewable",
    props: {
      dimensionViewable: Boolean,
    },
    events: {},
    render(moveable: MoveableManagerInterface) {
        const zoom = moveable.props.zoom;
        const rect = moveable.getRect();

        return (
          <Box
            key={"dimension-viewer"}
            className={"moveable-dimension"}
            sx={{
              left: `${rect.width / 2}px`,
              top: `${rect.height}px`,
              transform: `translate(-50%, ${20 * zoom!}px) scale(${zoom})`,
              position: "absolute",
              borderRadius: "2px",
              padding: "1px 3px",
              color: "white",
              fontSize: "13px",
              whiteSpace: "nowrap",
              fontWeight: "bold",
              willChange: "transform",
              backgroundColor: "#1976d2"
            }}
          >
            {Math.round(rect.offsetWidth)} x {Math.round(rect.offsetHeight)}
          </Box>
        )
    }
}

type ResizableTypeViewableProps =  {
  resizableTypeViewable?: boolean,
  elementResizableType: string,
  setElementResizableType: (type: string) => void
}
const ResizableTypeViewable = {
    name: "resizableTypeViewable",
    props: {
      resizableTypeViewable: Boolean,
      elementResizableType: String,
      setElementResizableType: Function
    },
    events: {},
    render(moveable: MoveableManagerInterface & { props: ResizableTypeViewableProps }) {
        const zoom = moveable.props.zoom;
        const rect = moveable.getRect();
        const elementResizableType = moveable.props.elementResizableType;
        const setElementResizableType = moveable.props.setElementResizableType;

        return (
          <Box
            key={"resizable-type-viewer"}
            className={"viewable-wrapper"}
            sx={{
              left: `${rect.width + 30}px`,
              top: `${rect.height / 2}px`,
              transform: `translate(-50%, ${20 * zoom!}px) scale(${zoom})`,
              position: "absolute",
              borderRadius: "2px",
              padding: "1px 3px",
              color: "white",
              fontSize: "13px",
              whiteSpace: "nowrap",
              fontWeight: "bold",
              willChange: "transform",
              backgroundColor: "#1976d2",
              p: 0.5
            }}
          >
            <Box
              className="viewable-content"
              onClick={() => setElementResizableType('resizable')}
              sx={{
                height: "22px",
                width: "22px",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                borderRadius: "5px",
                backgroundColor: elementResizableType === 'resizable' ? "#fff" : "inherit",
                color: elementResizableType === 'resizable' ? "#1976d2" : "#fff",
                cursor: "pointer",
              }}
            >
              ■
            </Box>
            <Box
            className="viewable-content"
            onClick={() => setElementResizableType('warpable')}
            sx={{
              height: "22px",
              width: "22px",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              borderRadius: "5px",
              backgroundColor: elementResizableType === 'warpable' ? "#fff" : "inherit",
              color: elementResizableType === 'warpable' ? "#1976d2" : "#fff",
              cursor: "pointer",
            }}
            >
              ◆
            </Box>
          </Box>
        )
    }
}

