import React from 'react';
import './App.css';

import {
  MoveableEditor,
  MoveableEditorV2,
  Preview
} from "./pages";

import { BrowserRouter, Route, Routes } from "react-router-dom";
console.log(process.env.PUBLIC_URL)
function App() {
  return (
    <BrowserRouter basename={process.env.PUBLIC_URL}>
      <Routes>
        <Route path="/" element={<MoveableEditor />} />
        <Route path="/v2" element={<MoveableEditorV2 />} />
        <Route path="/preview" element={<Preview />} />
      </Routes>
    </BrowserRouter>
  );
}

export default App;
