const ElementMoveable: React.FC<{
  wrapper: HTMLDivElement | null,
  containerRef: React.MutableRefObject<HTMLElement>,
  selectedElementType: string | null,
  selectedTargets: Array<HTMLElement | SVGElement>,
  elementResizableType: string,
  setElementResizableType: (type: string) => void
}> = ({
  wrapper,
  containerRef,
  selectedElementType,
  selectedTargets,
  elementResizableType,
  setElementResizableType
 }) => {

  const bounds = useMemo(() => {
    const containerRect = containerRef.current.getBoundingClientRect()
    if (wrapper) {
      const wrapperRect = wrapper.getBoundingClientRect()
      const wrapperRight = wrapperRect.right - containerRect.left
      const wrapperLeft = wrapperRect.left - containerRect.left
      const wrapperBottom = wrapperRect.bottom - containerRect.top
      return { left: wrapperLeft, right: wrapperRight, top: 0, bottom: wrapperBottom }
    } else {
      return {}
    }
  }, [selectedTargets])

  const elementGuidelines = useMemo(() => {
    return [wrapper, ...(wrapper ? Array.from(wrapper.children) : [])].filter(el => {
        return selectedTargets.indexOf(el as any) === -1;
    });
  }, [selectedTargets])
  return (
    <Moveable<DimensionViewableProps&ResizableTypeViewableProps>
      ables={[DimensionViewable, ResizableTypeViewable]}
      dimensionViewable={true}
      resizableTypeViewable={selectedElementType === "rect"}
      elementResizableType={elementResizableType}
      setElementResizableType={setElementResizableType}
      targets={selectedTargets}
      container={containerRef.current}
      origin={false}
      edge={false}
      draggable={true}
      rotatable={true}
      resizable={elementResizableType === 'resizable'}
      clippable={elementResizableType === 'warpable'}
      clipArea={true}
      clipVerticalGuidelines={[0, "50%", "100%"]}
      clipHorizontalGuidelines={[0, "50%", "100%"]}
      clipTargetBounds={true}
      defaultClipPath={"polygon"}
      onClip={(e) => {
        if (e.clipType === "rect") {
          e.target.style.clip = e.clipStyle;
        } else {
          e.target.style.clipPath = e.clipStyle;
        }
      }}
      roundable={true}
      snappable={true}
      bounds={bounds}
      throttleDrag={0}
      snapDirections={{ top: true, left: true, right: true, center: true, middle: true }}
      elementSnapDirections={{ top: true, left: true, right: true, center: true, middle: true }}
      snapGap={false}
      isDisplayInnerSnapDigit={true}
      elementGuidelines={elementGuidelines as any}
      onDragStart={({ target, clientX, clientY }) => {}}
      onDrag={({
          target,
          beforeDelta, beforeDist,
          left, top,
          right, bottom,
          delta, dist,
          transform,
          clientX, clientY,
      }: OnDrag) => {
          const wrapper = containerRef.current.querySelector('.wrapper') as HTMLElement
          const wrapperRect = wrapper.getBoundingClientRect()
          const wrapperHeight = wrapperRect.height
          const wrapperWidth = wrapperRect.width

          target!.style.left = `${left / wrapperWidth * 100}%`;
          target!.style.top = `${top / wrapperHeight * 100}%`;
      }}
      onDragEnd={({ target, isDrag, clientX, clientY }) => {}}
      onDragGroupStart={({ events }) => {}}
      onDragGroup={({ targets, events }) => {
        events.forEach(({ target, beforeTranslate, left, top }, i) => {
            const wrapper = containerRef.current.querySelector('.wrapper') as HTMLElement
            const wrapperRect = wrapper.getBoundingClientRect()
            const wrapperHeight = wrapperRect.height
            const wrapperWidth = wrapperRect.width
            target.style.top = `${top / wrapperHeight * 100}%`;
            target.style.left = `${left / wrapperWidth * 100}%`;
        });
      }}
      onDragGroupEnd={({ targets, isDrag, clientX, clientY }) => {}}
      keepRatio={false}
      throttleResize={0}
      onResizeStart={({ target , clientX, clientY}) => {}}
      onResize={({
          target, width, height,
          dist, delta, direction,
          clientX, clientY
      }: OnResize) => {
          const containerRect = containerRef.current.getBoundingClientRect()
          const wrapper = containerRef.current.querySelector('.wrapper') as HTMLElement
          const wrapperRect = wrapper.getBoundingClientRect()
          const wrapperTop = containerRect.top
          //elementがposition: absolute;なのでresizeの挙動がおかしい＆boundsでの制限がうまく効かなくなるので向きによって位置も調整するようにする
          if (direction[0] === -1 && direction[1] === -1) {
            delta[0] && (target!.style.width = `${width}px`) && (target!.style.left = `${(clientX - wrapperRect.left) / wrapperRect.width * 100}%`)
            delta[1] && (target!.style.height = `${height}px`) && (target!.style.top = `${(clientY - wrapperTop) / wrapperRect.height * 100}%`)
          } else {
            if (direction[0] === -1 && (direction[1] === 0 || direction[1] === 1) ) {
              delta[0] && (target!.style.width = `${width}px`) && (target!.style.left = `${(clientX - wrapperRect.left) / wrapperRect.width * 100}%`)
            } else {
              delta[0] && (target!.style.width = `${width}px`)
            }
            if (direction[1] === - 1 && (direction[0] === 0 || direction[0] === 1)) {
              delta[1] && (target!.style.height = `${height}px`) && (target!.style.top = `${(clientY - wrapperTop) / wrapperRect.height * 100}%`)
            } else {
              delta[1] && (target!.style.height = `${height}px`)
            }
          }
      }}
      onResizeEnd={({ target, isDrag, clientX, clientY }) => {}}
      throttleScale={0}
      onScaleStart={({ target, clientX, clientY }) => {}}
      onScale={({
          target, scale, dist, delta, transform,
          clientX, clientY,
      }: OnScale) => {
          target!.style.transform = transform;
      }}
      onScaleEnd={({ target, isDrag, clientX, clientY }) => {}}
      throttleRotate={0}
      onRotateStart={({ target, clientX, clientY }) => {
          console.log("onRotateStart", target);
      }}
      onRotate={({
          target,
          delta, dist,
          transform,
          clientX, clientY,
      }: OnRotate) => {
          console.log("onRotate", dist);
          target!.style.transform = transform;
      }}
      onRotateEnd={({ target, isDrag, clientX, clientY }) => {}}
      onWarpStart={({ target, clientX, clientY }) => {}}
      onWarp={({
          target,
          clientX,
          clientY,
          delta,
          dist,
          multiply,
          transform,
      }) => {
          target.style.transform = transform;
      }}
      onWarpEnd={({ target, isDrag, clientX, clientY }) => {
          console.log("onWarpEnd", target, isDrag);
      }}
      onRound={e => {
        e.target.style.borderRadius = e.borderRadius;
      }}
      onClick={e => {
          const target = e.inputTarget as any;
          if (e.isDouble && target.hasAttribute('contenteditable')) {
            target.contentEditable = "true"
            target.focus();
          }
      }}
      />
  )
}

