export const createUniqueString = () => {
  return Date.now().toString(36) + Math.random().toString(36).substr(2);
}

export const getPropertyDivideValue = (value: string) => {
  if (!value) return { value: 0, unit: '' }
  const intValue = parseInt(value, 10)
  const unit = value.replace(`${intValue}`, '')
  return { value: intValue, unit: unit }
}

export const getTextShadowPropertyDivideValue = (value: string) => {
  if (value === "none") return { x: '0', y: '0', blur: '0', color: '#000' }
  const valueArray = value.split(' ')
  let color = ''
  let x = '0'
  let y = '0'
  let blur = '0'
  if (valueArray[0].includes('rgb(')) {
    color = valueArray[0] + valueArray[1] + valueArray[2]
    x = valueArray[3]
    y = valueArray[4]
    blur = valueArray[5]
  }
  if (valueArray[0].includes('rgba(')) {
    color = valueArray[0] + valueArray[1] + valueArray[2] + valueArray[3]
    x = valueArray[4]
    y = valueArray[5]
    blur = valueArray[6]
  }
  return { color: color, x: x, y: y, blur: blur }
}

export const getBoxShadowPropertyDivideValue = (value: string) => {
  if (value === "none") return { x: '0', y: '0', blur: '0', spread: '0', color: '#000' }
  const valueArray = value.split(' ')
  let color = ''
  let x = '0'
  let y = '0'
  let blur = '0'
  let spread = '0'
  let inset = ''
  if (valueArray[0].includes('rgb(')) {
    color = valueArray[0] + valueArray[1] + valueArray[2]
    x = valueArray[3]
    y = valueArray[4]
    blur = valueArray[5]
    spread = valueArray[6]
    inset = valueArray[7]
  }
  if (valueArray[0].includes('rgba(')) {
    color = valueArray[0] + valueArray[1] + valueArray[2] + valueArray[3]
    x = valueArray[4]
    y = valueArray[5]
    blur = valueArray[6]
    spread = valueArray[7]
    inset = valueArray[8]
  }
  return { color: color, x: x, y: y, blur: blur, spread: spread, inset: inset }
}

export const checkInput = (target: HTMLElement | SVGElement) => {
    const tagName = target.tagName.toLowerCase();

    return (target as HTMLElement).isContentEditable || tagName === "input" || tagName === "textarea";
}

export const targetParentNodeByTagName = (currentNode: HTMLElement | SVGElement, targetSelecor: string) => {
  if (currentNode.tagName === targetSelecor) return currentNode;
  let targetNode: HTMLElement | SVGElement | string = ''

  while (currentNode.parentNode){
    if (currentNode.tagName === targetSelecor) {
      targetNode = currentNode
      break;
    }
    currentNode = currentNode.parentNode as HTMLElement;
  }
  return targetNode as HTMLElement | SVGElement
};
